
import { toggleMenu } from "../Libs";
import { Link } from "react-router-dom";

function SideMenu(props) {

    const closeOnClickOutside = (e) => {
        const targetEl = e.target;
        console.log("Target: ", targetEl);
        if (targetEl.className.includes("menu-wrapper open")) {
            targetEl.classList.remove("open");
        }
    };

    return (
        <section className="menu-wrapper" onClick={closeOnClickOutside}>
            <div className="menu-wrapper-content">
                <div className="menu-wrapper-content-hdr">
                    <div>
                        <a href="/">
                            <img src="/images/android-chrome-512x512.png" alt="company logo" />
                        </a>
                    </div>
                    <div onClick={toggleMenu} className="side-menu-close-btn">X</div>
                </div>
                <nav className="menu-wrapper-content-items">
                    <div className="bold">Main</div>
                    <div>
                        <a href="/" className='Home_page'>Home</a>
                    </div>
                    <div>
                        <Link to="/services" onClick={toggleMenu}>Services</Link>
                    </div>
                    <div>
                        <Link to="/contact" onClick={toggleMenu}>Contact</Link>
                    </div>
                    <div className="bold">Online Courses</div>
                    <div>
                        <Link to="/tutorials-source-code" onClick={toggleMenu}>Source Code</Link>
                    </div>
                    <div>
                        <Link to="/tutorials" onClick={toggleMenu}>Tutorials</Link>
                    </div>
                    <div>
                        <a href="https://www.youtube.com/@CodingAqyanoos" onClick={toggleMenu} target="_blank" rel="noreferrer noopener">YouTube</a>
                    </div>

                    <div className="bold">Tools</div>
                    <div>
                        <a href="/online-text-or-code-editor-notepad.html" >Online Notepad</a>
                    </div>
                    <div>
                        <Link to="/how-to-convert-decimal-to-hex" onClick={toggleMenu}>Hex / Decimal Convertor</Link>
                    </div>
                    <div className="bold">More...</div>
                    <div>
                        <Link to="/about" onClick={toggleMenu}>About</Link>
                    </div>
                    <div>
                        <Link to="/privacy-policy" onClick={toggleMenu}>Privacy Policy</Link>
                    </div>
                    <div>
                        <a href="/best-books-for-programmers-amazon.html" onClick={toggleMenu}>Best Books for You</a>
                    </div>

                    <div>
                        <a href="/best-laptop-for-students-amazon.html" onClick={toggleMenu}>Best Laptops for You</a>
                    </div>
                </nav>
            </div>

        </section>
    );
}

export default SideMenu;
